      .col-lg-10
        p.mb-3  El activo es lo que se posee, es decir, los bienes posedos y los derechos que se convertirn en cosas posedas en un futuro.
        p.mb-3  El pasivo y el patrimonio neto suponen cmo financiar aquello que se desea poseer, mediante deudas, prstamos, aportaciones de los propietarios, etc., como se muestra a continuacin:

    .row.mb-5.justify-content-center
      .col-lg-8
        figcaption.customCaption  <b>Figura</b> Estructura econmica y financiera de la unidad productiva.
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-i2.svg' alt='imagen decorativa')        
        figcaption  Nota. SENA (2021).


    h3.titulo-tercero
      i.fas.fa-angle-right
      | &nbsp;  Ingresos, gastos y costos

    .row.mb-5
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-ic2.png' alt='imagen decorativa')
      .col-lg-10
        p.mb-3  En toda contabilidad de una unidad productiva se debe identificar y tener claro los conceptos relacionados a gastos y costos. La diferencia entre estos, recae en que los costos son considerados inversiones que se identifican directamente con los ingresos, mientras que los gastos no se asocian con el retorno de la venta del producto o servicio de la unidad productiva. En pocas palabras, los costos se consideran activos, mientras que los gastos no. A continuacin, se conocer un poco ms de estos conceptos: 

    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-contenido.p-4(titulo="Ingresos")
        .row
          .col-lg-4
            figure.slideInLeft
              img(src='@/assets/curso/t5-t1.png' alt='imagen decorativa')
          .col-lg-8
            h4 Ingresos
            p Son incrementos en activos o disminuciones en pasivos.  Las causas de los ingresos son operaciones de ventas de bienes y servicios provenientes de las operaciones de la empresa que se orientan a los objetivos y metas.

      .tarjeta.color-acento-contenido.p-4(titulo="Costos")
        .row
          .col-lg-4
            figure.slideInLeft
              img(src='@/assets/curso/t5-t2.png' alt='imagen decorativa')
          .col-lg-8
            h4 Costos
            p Sacrificios econmicos que demanda la adquisicin de un bien o servicio con utilidad econmica para la empresa. Son las inversiones que hace la entidad para realizar su objetivo. Aqu se encuentran los costos de ventas cantidad que invierte la empresa para lograr su objetivo, es una inversin realizada que influye directamente en el precio del producto o servicio que ofrece la empresa.

      .tarjeta.color-acento-contenido.p-4(titulo="Gastos")
        .row
          .col-lg-4
            figure.slideInLeft
              img(src='@/assets/curso/t5-t3.png' alt='imagen decorativa')
          .col-lg-8
            h4 Gastos
            p Es todo aquel desembolso de dinero que hace la entidad para lograr su objetivo, sin embargo, a diferencia del costo, estos desembolsos no incurren en el precio del producto o servicio que ofrece la entidad.





    #t_5_1.titulo-segundo.color-secundario
      h2 5.1  Ventas, administracin y logstica

    p.mb-5  Para el estado de resultados es de gran importancia que en cada una de estas reas: ventas, administracin y logstica se desarrolle de la mejor forma que le permita a la unidad productiva reflejar al final resultados favorables. A continuacin,  se va a conocer en ms detalle cada uno de ellos:
    
    h3.titulo-tercero
      i.fas.fa-angle-right
      | &nbsp;  Ventas
    .row.mb-5
      .col-lg-8
        p.mb-3  El concepto de ventas es muy amplio y se encuentran varios en diferentes bibliografas, pero se entiende las ventas como: las actividades realizadas para incentivar potenciales clientes a realizar una determinada compra. Su gestin, se encarga de realizar actividades de <i>marketing</i> estratgico para la venta de bienes y servicios.
        p.mb-3  El rea de ventas de toda unidad productiva debe establecer objetivos tcticos de ventas, desarrollar un plan de accin y supervisin, planificar y gestionar recursos necesarios para soportes de ventas en curso. Para lograr el nivel de ventas esperado, se establecen elementos claves desde las actividades de la administracin para la gestin de ventas. A continuacin, se puede ampliar ms al respecto: 
      .col-lg-4
        figure.slideInLeft
          img(src='@/assets/curso/t5-ic5.png' alt='imagen decorativa')
    
    .row.mb-5.justify-content-center
      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
