          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Identificacin del consumidor final
              p.small Clientes objetivo; segmentos; grupos destinatarios; nichos estratgicos; sectores de apoyo.

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center.indicador__container(data-aos="flip-up")
          img(src="@/assets/curso/t5-c2.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Tcticas para atraer al consumidor final  
              p.small Los clientes de la competencia. Despus de esto se definen tcticas para retener los clientes, ampliar la propuesta planteada y definir estrategias ante una reclamacin, entre otras. 
          .indicador--click(v-if="mostrarIndicador")

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c3.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Estructurar canales de distribucin y venta 
              p.small Definir los canales de distribucin efectivos que permita una distribucin intensiva y eficaz de las mercancas. 

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c4.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Gestionar canales de venta
              p.small Mantener ventas en los establecimientos; identificar las condiciones de cada canal; gestionar la estimulacin y la comunicacin, etc.

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c5.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Estimular los canales de distribucin
              p.small Planificacin y promocin de la distribucin entre los participantes del canal, establecer metodologas de ventas en los canales de distribucin.

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c6.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Organizacin de las ventas 
              p.small Estructurar las ventas de la unidad productiva, definir tareas y funciones de la organizacin para la gestin de ventas (estructura, personal del rea de ventas; distribucin de las funciones: por territorios, clientes, lneas de productos, etc.).

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c7.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Administracin de ventas
              p.small Se debe tener una planificacin y control de los empleados; de igual forma generar una contratacin, motivacin; anlisis de las actividades; seleccin y adaptacin (informacin, evaluacin del rea o departamento).

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c8.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Gestin de ventas
              p.small Calcular coste de ventas, determinar el gasto en las ventas; establecer los canales de distribucin, compras, rentabilidad de las ventas y evaluacin de la eficiencia de los empleados.

      .col-md-4.col-lg-3.mb-3
        .nav-holder.align-items-center(data-aos="flip-up")
          img(src="@/assets/curso/t5-c9.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Sistema de ventas
              p.small Se debe realizar de forma peridica una evaluacin y ajuste de todo el sistema de ventas (al menos una vez al ao).

    p.mb-5  Por otra parte, se encuentra que para que exista una gestin eficaz de las ventas se debe identificar el cliente objetivo, realizar un <i>marketing</i> comercial y gestionar la distribucin de los productos. Todo esto determina la estructura de apoyo de las ventas y para su gestin implica: 

    .row.mb-5.justify-content-center
      .col-lg-8
        figcaption.customCaption.mb-3 <b>Figura</b> Gestin de Ventas
        figure(data-aos='flip-up')
