    h3.titulo-tercero
      i.fas.fa-angle-right
      | &nbsp;  La administracin
    .row.mb-5
      .col-lg-8
        p.mb-3  Toda unidad productiva debe realizar y tener una administracin que le permita establecer los procesos, identificar las fortalezas, debilidades y oportunidades que tiene la organizacin ante las dems en el mercado, pero, adems permitir conocer la situacin financiera real de la organizacin.
        p.mb-3.text-bold  En cuanto a su conceptualizacin, se puede decir que la administracin es el proceso que busca por medio de la planificacin, la organizacin, ejecucin y el control de los recursos darles un uso ms eficiente para alcanzar los objetivos de la unidad productiva.
        p.mb-3  Por otra parte, para que la administracin pueda alcanzar estos objetivos, se debe hacer uso de los recursos humanos, intelectuales, materiales, tecnolgicos y financieros que tiene la unidad productiva; esto con el fin de buscar estabilidad y crecimiento de la organizacin y grupos sociales.
        p.mb-3  Segn lo planteado anteriormente, el administrador es fundamental en la unidad productiva. Las actividades administrativas se caracterizan por tener una estructura de subordinacin o relacin de jerarqua, en otras palabras, se trabaja bajo el mando de otro. En la actualidad se puede observar que la administracin se puede aplicar en instituciones formales e informales, las formales son aquellas que se rigen por normas y leyes que estn escritas para que se pueda dar su funcionamiento como es el caso del Estado, empresa, organizacin o unidad productiva. Por otra parte, las informales tienen normas, pero no se encuentran escritas, un ejemplo de esto puede ser la familia donde tambin se administran recursos bajos un objetivo.
        
      .col-lg-4
        figure(data-aos='flip-up')
          img(src='@/assets/curso/t5-i6.jpg' alt='imagen decorativa')     
    p.mb-5  A continuacin, se observa la importancia de la administracin: 

    figcaption.customCaption.mb-3 <b>Figura</b> La importancia de la administracin
    .row.mb-5.justify-content-center.no-gutters        
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-p1.svg' alt='imagen decorativa')   
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-p2.svg' alt='imagen decorativa')   
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-p3.svg' alt='imagen decorativa')         
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-p4.svg' alt='imagen decorativa')        
      .col-lg-2
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-p5.svg' alt='imagen decorativa')    
    figcaption.mb-5  Nota. SENA (2021).

    p.mb-5  Por otra parte, se encuentran las caractersticas ms importantes de la administracin, estas son:
    .tarjeta.p-4.bg-slider.mb-5.shadow-box
      SlyderA
        .row
          .col-lg-6.mb-4.mb-md-0
            h3 La universalidad
            p La administracin puede ser utilizada en cualquier tipo de institucin, unidad productiva o grupo social. Puede ser una institucin formal, informal, privada, pblica entre otros. Aqu todas quieren alcanzar sus objetivos usando correctamente los recursos.   
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s1.png' alt='Ejemplo de factura') 

        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Es interdisciplinaria 
            p Se relaciona con cualquier ciencia que use mtodos, principios y procesos que utilice los recursos de forma eficiente. 
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s2.png' alt='Ejemplo de factura') 

        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Medio como fin
            p Es un instrumento que se utiliza para lograr un fin determinado, las organizaciones o unidad productiva que la aplica busca alcanzar sus propios objetivos.
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s3.png' alt='Ejemplo de factura') 

        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Tiene jerarqua
            p Su funcionamiento est basado de forma jerrquica, debe haber un jefe que este al mando y una serie de subordinados, el encadenamiento de sus esfuerzos permite cumplir con una meta comn.
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s4.png' alt='Ejemplo de factura') 

        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Tiene unidad temporal
            p El proceso administrativo es dinmico puesto que no se realizan de forma aislada, sino que operan de forma simultnea.
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s5.png' alt='Ejemplo de factura') 

        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Su aplicabilidad 
            p Se puede operar en todos los niveles de la organizacin, donde existe jerarqua. Un ejemplo puede ser una ama de casa, el precedente de una compaa o de un pas.
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s6.png' alt='Ejemplo de factura') 
        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Es especfica
            p La administracin se relaciona con otras ciencias y tcnicas, pero no puede confundir con otras reas porque tiene un carcter especfico.
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s7.png' alt='Ejemplo de factura') 
        .row
          .col-lg-6.mb-4.mb-md-0
            h3 Es flexible
            p Se puede aplicar en cualquier tipo de organizacin independientemente del campo al que se dedica y se adaptan a sus necesidades. 
          .col-lg-6
            figure(data-aos='flip-left')
              img(src='@/assets/curso/t5-s8.png' alt='Ejemplo de factura') 
    br
    p.mb-5  Se pueden encontrar diferentes tipos de administracin.
    .row.mb-5.justify-content-center
