          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Pblica
              p.small Se encarga de la gestin de las instituciones del Estado.

      .col-md-4.col-lg-4.mb-3
        .nav-holder.align-items-center(data-aos="flip-left")
          img(src="@/assets/curso/t5-f2.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Privada
              p.small Se dedica a la administracin de entidades privadas que no dependen del Gobierno.

      .col-md-4.col-lg-4.mb-3
        .nav-holder.align-items-center(data-aos="flip-left")
          img(src="@/assets/curso/t5-f1.svg", alt="Imagen decorativa")
          .text.p-lg-3.p-4
            .row.px-lg-3.mb-lg-2
              p.h3 Mixta
              p.small Se enfoca en la gestin de instituciones privadas que reciben apoyo del Estado, o de organismos autnomos donde el Gobierno tiene cierta injerencia o participacin.


    .row.mb-5.justify-content-center
      .col-lg-10
        .cajon.color-primario.p-4
          p.mb-0  De igual modo, se puede clasificar la administracin segn la materia que se est gestionando, pudiendo ser <b>financiera</b> (si se trata de capitales), <b>comercial</b> (en el rea de <i>marketing</i>), de <b>procesos</b>, de <b>recursos humanos</b>, etc.

    h3.titulo-tercero
      i.fas.fa-angle-right
      | &nbsp;  Logstica
    
    .row.mb-5
      .col-lg-8
        p.mb-4  La logstica se convierte en un factor fundamental para toda unidad productiva. Antes de hablar un poco ms sobre ella se va a conocer su conceptualizacin.
        .bloque-texto-b.color-primario.p-4.mb-5
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h3.mb-0 La logstica es la parte del proceso de la cadena de suministros que planea, lleva a cabo y controla el flujo y almacenamiento eficientes y efectivos de bienes y servicios, as como de la informacin relacionada, desde el punto de origen hasta el punto de consumo, con el fin de satisfacer los requerimientos de los clientes.
            i.fas.fa-quote-right
          .bloque-texto-d__autor 
            .h6.mb-0 - Segn el Consejo de Direccin Logstica  CML (1962)
        p.mb-3  En el proceso de logstica se encuentra la <b>cadena de suministros</b> que enfatiza las interacciones que tienen lugar entre las funciones de <i>marketing</i>, logstica y produccin en una unidad productiva, y las interacciones que se llevan a cabo entre empresas independientes que se encuentran dentro del canal de flujo del producto. Su funcionamiento abarca todas las actividades relacionadas con el flujo y transformacin de bienes, desde la etapa de materia prima (extraccin) hasta el usuario o consumidor final, as como los flujos de informacin relacionados. Por otra parte, los materiales y la informacin fluyen en sentido ascendente y descendente en la cadena de suministros. La mejora de sus relaciones permite alcanzar una ventaja competitiva sostenible.
      
      .col-lg-4
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-i10.jpg' alt='imagen decorativa') 

    .row.mb-5.justify-content-center
      .col-lg-8
        figcaption.customCaption <b>Figura</b> Cadena de suministros
        figure(data-aos='flip-up')
          img(src='@/assets/curso/t5-i13.svg' alt='imagen decorativa')         
        br
        figcaption  Nota. SENA (2021).

    
    .row.mb-5.justify-content-center
      .col-lg-8
        p.mb-3  A continuacin, se conocern las actividades que conforman la logstica de una unidad productiva (proceso de la cadena de suministros) es importante tener en cuenta que estas varan de una empresa a otra, dependiendo de la estructura organizacional de cada una.
        .h4 Las actividades de un sistema tpico de logstica son:
        p.mb-3  Servicios al cliente, pronstico de la demanda, comunicaciones de distribucin, control de inventarios, manejo de materiales, procesamiento de pedidos, seleccin de la ubicacin de fbricas y almacenamiento (localizacin), compras, embalaje, manejo de bienes devueltos, eliminacin de desperdicios, trfico, transporte y almacenamiento.
      .col-lg-4
        figure(data-aos='flip-up')
          img(src='@/assets/curso/t5-i11.jpg' alt='imagen decorativa')      

    .row.mb-5.justify-content-center
      .col-lg-4
        figure(data-aos='flip-up')
          img(src='@/assets/curso/t5-i12.svg' alt='imagen decorativa')           

      .col-lg-8
        .h4 Logstica de negocios
