        p.mb-4  Antes de entrar en profundidad, se debe conocer el concepto de balance, se puede decir, que es un contable que va a permitir conocer la situacin financiera y econmica de una empresa en un momento determinado del tiempo. El Balance se compone de tres factores, estos se conocen como: Activo, Pasivo y Patrimonio (Capital).
        .cajon.color-acento-contenido.p-4
