    p.mb-5  Como se puede observar en la estructura anterior, la nica diferencia es que la precisin de datos es mayor. Esto se da porque los saldos estaran ajustados de acuerdo con las correcciones pertinentes. A continuacin, se dispone un ejemplo de balance de apertura: 
    
