            h3 Factura
            p Se constituye como un ttulo valor, que el vendedor del servicio podr librar y entregar al comprador o beneficiario del servicio, el objetivo de este documento es certificar las operaciones de consumo efectuada. Como requisitos mnimos debe contemplar: la fecha de la operacin, el nombre de las partes, la descripcin del producto o servicio, el valor del negocio, la forma de pago, entre otros aspectos. Se entrega la factura original al cliente y se elaboran dos copias, una para el archivo y otra para el comprobante diario de contabilidad (software o digital).    
