            h3 Recibo de caja  
            p Es un documento contable que se utiliza para registrar los diferentes recaudos de dinero que tenga la unidad productiva por concepto de cualquier transaccin comercial. Aqu se contabiliza un debito a la cuenta caja y el crdito segn el concepto de pago. El original se le entrega al cliente y se guarda una copia para el archivo consecutivo y diario de contabilidad. Como requisitos mnimos, debe llevar la fecha, los conceptos , datos de la unidad productiva, numeracin consecutiva, el monto y total de la operacin.   
