        p.mb-3  Por ejemplo: <b>la cotizacin</b>, tambin conocida como presupuesto o propuesta econmica, es un documento de carcter informativo, que establece el precio de un bien o servicio como mecanismo de negociacin de compra, consideraciones de carcter presupuestario, entre otras. Este documento no genera registro contable y puede solicitarse de forma fsica o virtual por medio de correo electrnico o medio digital. Este puede llegar a tener la misma estructura en su formato a la de una factura, pero en su informacin se indica su razn, como se puede observar a continuacin: 
          
