        p.mb-3  Un ttulo de valor implica que la persona que suscribe el titulo est obligada a la prestacin del poseedor del ttulo, el tenedor es beneficiario y puede mediante accin cambiaria exigir jurdicamente el pago del contenido literal del ttulo de valor. Un ejemplo de esto es: quien firma una letra de cambio, se encuentra obligado a pagarla en la fecha estipulada y el beneficiario podr cobrarla jurdicamente si se niega el deudor.  
